@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
html,
body, #root {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #121212;
  color: #fff;
}

.font-header {
  font-family: "Lato";
}

.font-primary {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.align-screen--vertical-centered {
  display: flex;
  align-items: center;
  height: 100%;
}

.align-screen--centered {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.home {
  padding-left: 5rem;
  display: flex;
  cursor: default;
}

.home .container {
  display: flex;
  letter-spacing: 0.1rem;
}

.home-greeting {
  font-weight: 700;
  font-size: 5rem;
  padding-bottom: 1rem;
}

.home-greeting div {
  height: 5rem !important;
}

.home-introduce {
  font-weight: 700;
  font-size: 2rem;
  padding-bottom: 1.25rem;
}

.home-introduce div {
  height: 2rem !important;
}

.home-role {
  letter-spacing: 0rem !important;
  opacity: 0.9;
  padding-bottom: 1.5rem;
}

.home-role div {
  height: 1rem !important;
}

.icon {
  fill: #fff;
  opacity: 0.8;
  width: 20px;
  height: 20px;
}

.icon:hover {
  fill: #2be2ff;
  opacity: 1;
}

.home-socials {
  display: flex;
}

.home-socials div {
  width: 20px !important;
  height: 20px !important;
  padding-right: 1rem;
}

@media screen and (max-width: 760px) {
  .home-greeting {
    font-size: 4rem;
    padding-bottom: 0rem;
  }

  .home-greeting div {
    height: 4rem;
  }

  .home-introduce {
    font-size: 1.5rem;
    letter-spacing: 0.05rem !important;
    padding-bottom: 0.75rem;
  }

  .home-introduce div {
    height: 1.5rem;
  }

  .home-role {
    font-size: 0.9rem;
    padding-bottom: 1rem;
  }

  .home-role div {
    height: 0.9rem;
  }

  .icon {
    width: 16px;
    height: 16px;
  }

  .home-socials div {
    width: 16px !important;
    height: 16px !important;
    padding-right: 0.75rem;
  }
}

@media screen and (max-width: 640px) {
  .home-greeting {
    font-size: 3rem;
    padding-bottom: 0.25rem;
  }

  .home-greeting div {
    height: 3rem !important;
  }

  .home-introduce {
    font-size: 1.25rem;
    letter-spacing: 0.05rem !important;
    padding-bottom: 0.5rem;
  }

  .home-introduce div {
    height: 1.25rem !important;
  }

  .home-role {
    font-size: 0.8rem;
  }

  .home-role div {
    height: 0.8rem !important;
  }

  .icon {
    width: 15px;
    height: 15px;
  }

  .home-socials div {
    width: 15px !important;
    height: 15px !important;
    padding-right: 0.5rem;
  }
}

@media screen and (max-width: 480px) {
  .home {
    padding-left: 2rem;
  }
}

@media screen and (max-width: 400px) {
  .home {
    padding-left: 1rem;
  }

  .home-greeting {
    font-size: 2.75rem;
  }

  .home-greeting div {
    height: 2.75rem !important;
  }

  .home-introduce {
    font-size: 1rem;
  }
  
  .home-role {
    font-size: 0.75rem;
    /* padding: 0.25rem 0 0.5rem; */
  }

  .home-role div {
    height: 0.75rem !important;
  }

  .icon {
    width: 15px;
    height: 15px;
  }

  .home-socials div {
    width: 15px !important;
    height: 15px !important;
    padding-right: 0.5rem;
  }
}

.menu {
  display: flex;
  flex-direction: column;
  position: fixed; top: 0; right: 0;
  padding: 1rem;
}

.menu .icon {
  width: 40px;
  height: 40px;
  opacity: 0.5;
  padding-bottom: 0.5rem;
}

.menu .icon:hover {
  opacity: 1;
}
